import * as React from "react";
import { Layout, Section, Hero, AnimatedPictogram, FullVideoPlayer, ShellHomePreview } from "../components";
import { Board, TaskDetailPanel, type TaskDetailIssue } from "@korporus/site-ui";
import { FEATURE_ENTRIES } from "../content/features";
import { getVideoById } from "../content/videos";
import { getVideoSrc } from "../lib/getVideoSrc";

const BOARD_ISSUES: TaskDetailIssue[] = [
  {
    id: "day-01",
    title: "Publish the agent plan to the workflow board",
    status: "in_progress",
    type: "story",
    priority: 1,
    description: "The workflow layer records long-term goals and milestones in Git so agents and humans share one source of truth."
  },
  {
    id: "day-02",
    title: "Background agent updates execution status",
    status: "backlog",
    type: "story",
    priority: 1,
    description: "Agents post progress updates and status transitions to the workflow board as work runs asynchronously."
  },
  {
    id: "day-03",
    title: "Escalate blocked work with explicit owner",
    status: "closed",
    type: "story",
    priority: 2,
    description: "Blocked items are visible at the process layer, not buried in raw logs."
  },
  {
    id: "day-04",
    title: "Monitor project health above dashboards",
    status: "closed",
    type: "story",
    priority: 2,
    description: "Process monitoring stays at the workflow layer so teams can guide agents over long horizons."
  }
];

const IndexPage = () => {
  const columns = ["backlog", "in_progress", "closed"];
  const [issues, setIssues] = React.useState<TaskDetailIssue[]>(BOARD_ISSUES);
  const [selectedIssueId, setSelectedIssueId] = React.useState<string | null>(null);

  React.useEffect(() => {
    let step = 0;
    const timer = window.setInterval(() => {
      setIssues((current) => {
        const next = [...current];
        const rotatingIdx = next.findIndex((issue) => issue.id === "day-02");
        if (rotatingIdx >= 0) {
          const status = step % 3 === 0 ? "backlog" : step % 3 === 1 ? "in_progress" : "closed";
          next[rotatingIdx] = { ...next[rotatingIdx], status };
        }
        return next;
      });
      step += 1;
    }, 2600);

    return () => window.clearInterval(timer);
  }, []);

  const selectedIssue = issues.find((issue) => issue.id === selectedIssueId) ?? null;

  const introVideo = getVideoById("intro");
  const introPoster = introVideo?.poster ? getVideoSrc(introVideo.poster) : undefined;
  const introSrc = introVideo ? getVideoSrc(introVideo.filename) : "";

  return (
    <Layout>
      <Hero
        title="A new OS for a new kind of app"
        subtitle="Korporus is a UX paradigm for asynchronous AI agents: containerized and sandboxed execution, built-in workflow process monitoring through Git, and first-class programmable procedure development."
        rightPane={<ShellHomePreview scale={0.4} className="max-w-full" />}
        actions={
          <>
            <a href="/getting-started" className="cta-button px-6 py-3 text-sm transition-all hover:brightness-95">
              Get Started
            </a>
            <a href="/features" className="text-sm font-semibold leading-6 text-foreground hover:text-selected transition-all">
              Explore Features <span aria-hidden="true">→</span>
            </a>
          </>
        }
      />

      <div className="mx-auto max-w-3xl px-6 lg:px-8 text-center pb-24">
        <p className="text-xl leading-8 text-muted font-medium">
          Chat sessions are transient. Korporus gives agents a durable operating surface and integrates workflow oversight plus procedure tooling so teams can plan long-term work, monitor execution, and safely run programmable agent procedures.
        </p>
      </div>

      <Section title="What the experience looks like" subtitle="Consistent motion and layout patterns make the product feel coherent from the first click.">
        <div className="w-full max-w-5xl mx-auto h-auto">
          <AnimatedPictogram />
        </div>
      </Section>

      <Section
        title="Workflow process monitoring"
        subtitle="Use Git as an information bus for plans and status updates so you can supervise asynchronous agents at the workflow layer."
        variant="alt"
      >
        <div className="kanban-snapshot-container h-auto">
          <div className="grid gap-4 lg:grid-cols-[1fr_320px]">
            <Board columns={columns} issues={issues} onSelectIssue={(issue) => setSelectedIssueId(issue.id)} selectedIssueId={selectedIssueId} />
            <TaskDetailPanel task={selectedIssue} isOpen={Boolean(selectedIssue)} onClose={() => setSelectedIssueId(null)} />
          </div>
        </div>
      </Section>

      <Section
        title="Procedure-native development"
        subtitle="Korporus includes a first-class environment for designing and operating agent procedures, not just chat prompts."
      >
        <div className="grid gap-6 md:grid-cols-2">
          <div className="rounded-xl border border-border/60 bg-card p-6">
            <h3 className="text-lg font-semibold text-foreground mb-2">Agent-assisted IDE + Lua-based DSL</h3>
            <p className="text-muted leading-relaxed">
              Build procedures with context-engineering features, hybrid AI/ML plus programmatic logic, and built-in evaluation against high-level task goals.
            </p>
          </div>
          <div className="rounded-xl border border-border/60 bg-card p-6">
            <h3 className="text-lg font-semibold text-foreground mb-2">Secure runtime boundaries by default</h3>
            <p className="text-muted leading-relaxed">
              Run procedures in isolated sandboxes (for example e2b or simple containers) with a zero-knowledge broker that keeps secrets outside agent containers.
            </p>
          </div>
        </div>
      </Section>

      <Section title="How it works" subtitle="Intro video slot and draft poster are wired now; final production follows as a separate project.">
        <div className="w-full flex justify-center">
          <FullVideoPlayer src={introSrc} poster={introPoster} videoId="intro" />
        </div>
      </Section>

      <Section title="User Features" subtitle="High-level product value focused on daily usage and outcomes." variant="alt">
        <div className="grid gap-6 md:grid-cols-2">
          {FEATURE_ENTRIES.map((feature, index) => (
            <a key={feature.href} href={feature.href} className="group block h-full">
              <div className="bg-card rounded-2xl p-6 md:p-8 h-full flex flex-col transition-all duration-300 group-hover:bg-card-muted/50 group-hover:shadow-lg">
                <div className="mb-6 rounded-xl border border-border/50 bg-background p-4">
                  <p className="text-xs font-mono uppercase tracking-widest text-muted">Feature {String(index + 1).padStart(2, "0")}</p>
                  <div className="mt-3 h-12 rounded-lg bg-card-muted/70 border border-border/40" />
                </div>
                <div className="flex-1 flex flex-col">
                  <h3 className="text-2xl font-bold text-foreground mb-3 group-hover:text-selected transition-colors flex items-center gap-2">
                    {feature.title}
                    <span className="opacity-0 -translate-x-2 transition-all group-hover:opacity-100 group-hover:translate-x-0">→</span>
                  </h3>
                  <p className="text-muted leading-relaxed flex-1">{feature.description}</p>
                </div>
              </div>
            </a>
          ))}
        </div>
      </Section>

      <Section title="For Developers" subtitle="Need the technical architecture details? Korporus has a dedicated developer section.">
        <div className="grid gap-6 md:grid-cols-3">
          <div className="rounded-xl border border-border/60 bg-card p-6 md:col-span-2">
            <h3 className="text-lg font-semibold text-foreground mb-2">Developer-specific content lives separately</h3>
            <p className="text-muted leading-relaxed">
              The developer area covers manifests, module federation runtime loading, slot contracts, host integration, and deployment topology.
            </p>
          </div>
          <div className="rounded-xl border border-border/60 bg-card p-6 flex flex-col justify-between">
            <p className="text-muted text-sm mb-4">Switch to the technical track when you are designing, extending, or integrating Korporus.</p>
            <a href="/developers" className="cta-button px-5 py-2 text-sm text-center">Open Developer Section →</a>
          </div>
        </div>
      </Section>
    </Layout>
  );
};

export default IndexPage;
